.profile{
    display: flex;
    width: 100%;
    padding: 10px 13px 0 13px;
    box-sizing: border-box;
    background-color: black;
    color: white;
}

.profile_page{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    flex: 1;
    margin-left: 270px;
    color: white;
    margin-top: 56px;
    justify-content: center;
    align-items: center;
}

.profile_page_inactive{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    flex: 1;
    color: white;
    margin-top: 56px;
    justify-content: center;
    align-items: center;
}
.profile_top_section{
    width: 100%;
    display: flex;
    padding: 0 0 20px 0;
    margin-top: 56px;
}
.profile_top_section_profile{
    width: 15%;
    height: 60%;
}
.profile_top_section_img{
    width: 100%;
    border-radius: 50%;
    height: 100%;
}
.profile_top_section_about{
    display: flex;
    flex-direction: column;
    gap: 7px;
    width: 85%;
}
.profile_top_section_about_name{
    font-size: 36px;
    font-weight: 600;
    display: flex;
}
.profile_top_section_info{
    font-size: 16px;
    color: rgb(153, 153, 153);
}
.profile_videos{
    width: 100%;
}
.profile_video_title{
    font-size: 22px;
    padding-bottom: 10px;
    color: rgb(207, 206, 206);
    font-weight: 500;
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgb(153, 153, 153);
}
.profileVideos{
    display: flex;
    gap: 10px;
    height: 100vh;
    flex-wrap: wrap;
    margin-top: 20px;
}
.profileVideo_block{
    width: 210px;
    cursor: pointer;
}
.profileVideo_block_thumbnail{
    width: 100%;
    height: 140px;
}
.profileVideo_block_thumbnail_img{
    width: 100%;
    height: 100%;
}
.profileVideo_block_details{
    display: flex;
    flex-direction: column;
    font-weight: 600;
}
.profileVideo_block_details_about{
    color: #ababab;
    font-size: 13px;
}
.profile_right_section{
    font-size: initial;
    margin-left: 45%;
    display: flex;
}
.child_section{
    margin-right: 80px;
}


@media (max-width: 576px){
    .profile_top_section{
        width: 100%;
        display: flex;
        padding: 0 0 20px 0;
        margin-top: 50px;
    }
    .profile_top_section_profile{
        width: 25%;
        height: 40%;
    }
    .profile_top_section_img{
        width: 100%;
        border-radius: 50%;
        height: 90%;
    }
    .profile_top_section_about{
        display: flex;
        flex-direction: column;
        gap: 7px;
        width: 85%;
    }
    .profile_top_section_about_name{
        font-size: 36px;
        font-weight: 600;
        display: flex;
        flex-direction: column;
        padding-left: 0px;
    }
    .profile_right_section{
        font-size: initial;
        margin-left: 1%;
        display: flex;
    }
    .child_section{
        margin-left: 0px;
    }
    .profileVideos{
        display: flex;
        flex-direction: column;
        gap: 10px;
        height: 100vh;
        flex-wrap: wrap;
        margin-top: 20px;
    }
    .fullHomePage2{
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
        flex: 1;
        min-height: 100vh;
    }
    .homePage2{
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
        flex: 1;
        margin-left: 262px;
        min-height: 100vh;
    }
    
}