@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap');
.navbar{
    height: 56px;
    box-sizing: border-box;
    padding: 10px 16px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    top: 0;
    position: fixed;
    background-color: black;
    z-index: 10;
  }
  .navbar-left{
      gap: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: fit-content;
  }
  .navbarHamberger{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  .navbar_youtubeImg{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: white;
    text-decoration: none;
  }
  .navbar_youtubeImage{
    color: red;
  }
  .navbar_utubeTitle{
    font-size: 20px;
    word-spacing: 0px;
    letter-spacing: 0px;
    font-family: "Oswald", sans-serif;
    font-optical-sizing: auto;
    font-weight: weight;
    font-style: normal;
  }

  .navbar-middle{
    display: flex;
    gap: 10px;
    width: 50%;
  }
  .navbar-searchBox{
    width: 80%;
    display: flex;
  }
  .navbar_searchBox-form{
    width: 100%;
    height: 40px;
  }
  .navbar_searchBoxInput{
    width: 100%;
    height: 40px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border: 1px solid rgb(58, 57, 57);
    background-color: #121212;
    color: white;
    font-size: 16px;
    padding-left: 30px;
  }
  .navbar_searchIconBox{
    cursor: pointer;
    width: 70px;
    height: 43px;
    border: 1px solid rgb(42, 42, 42);
    background-color: rgb(42, 42, 42);
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  .navbar_mike{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(42, 42, 42);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    cursor: pointer;
  }

  .navbar-right{
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .navbar-right-logo{
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
  }
  .navbar-modal{
    position: absolute;
    top: 35px;
    width: 100%;
    z-index: 20;
    color: white;
  }
  .navbar-modal-option{
    background-color: rgb(85, 85, 85);
    padding: 10px;
    cursor: pointer;
    &:hover{
      background-color: rgb(34, 34, 34);
    }
  }
  .profile-img{
    height: 30px;
    width: 30px;
    border-radius: 50%;
  }
  .navbarHamberger1{
    display: none;
  }


  @media (max-width: 576px){
    .navbarHamberger1{
      display: block;
    }
    .navbarHamberger{
      display: none;
    }
    .navbar-modal1{
      position: absolute;
      top: 35px;
      right: 235px;
      width: 105px;
      z-index: 20;
      color: white;
    }
    .navbar_mike{
      display: none;
    }
    .navbar-modal{
      position: absolute;
      top: 35px;
      right: 20px;
      width: 100px;
      z-index: 20;
      color: white;
    }
    .navbar-modal-option{
      background-color: rgb(85, 85, 85);
      width: 100%;
      padding: 10px;
      cursor: pointer;
      &:hover{
        background-color: rgb(34, 34, 34);
      }
    }
    .navbar_searchIconBox{
      display: none;
    }
    .navbar_searchBoxInput{
      width: 100%;
      height: 40px;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px;
      border: 1px solid rgb(58, 57, 57);
      background-color: #121212;
      color: white;
      font-size: 16px;
      padding-left: 30px;
    }
  }