.video{
    margin-top: 56px;
    display: flex;
    padding: 30px 0;
    justify-content: center;
    background-color: black;
    color: white;
}
.videoPostSection{
    width: 100%;
    max-width: 875px;
    display: flex;
    flex-direction: column;
}
.video_youtube{
    width: 100%;
}
.video_youtube_video{
    width: 100%;
    border-radius: 10px;
    height: 450px;
}
.video_youtubeAbout{
    display: flex;
    flex-direction: column;
}
.video_utubeTitle{
    font-size: 20px;
    font-weight: 700;
}
.youtube_video_ProfileBlock{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}
.youtube_video_ProfileBlock_left{
    display: flex;
    gap: 15px;
}
.youtube_video_ProfileBlock_left_img{
    width: 35px;
    height: 35px;
    cursor: pointer;
}
.youtube_video_ProfileBlock_left_image{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.youtubeVideo_subsView{
    display: flex;
    flex-direction: column;
}
.youtubePostProfileName{
    font-weight: 500;
    font-size: 16px;
}
.youtubePostProfileSubs{
    font-size: 14px;
    color: #AAAAAA;
}
.subscribeBtnYoutube1{
    background-color: white;
    color: black;
    padding: 0px 16px;
    border-radius: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 36px;
    font-weight: 600;
    cursor: pointer;
    font-size: 14px;
}
.subscribedBtnYoutube1{
    background-color: rgb(244, 107, 107);
    cursor: not-allowed;
    pointer-events: none;
    color: white;
    padding: 0px 16px;
    border-radius: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 36px;
    font-weight: 600;
    cursor: pointer;
    font-size: 14px;
}
.youtube_video_likeBlock{
    display: flex;
    gap: 10px;
    background-color: #a5a5a538;
    justify-content: center;
    align-items: center;
    padding: 10px 10px;
    box-sizing: border-box;
    border-radius: 18px;
    cursor: pointer;
    margin-left: 10px;
}
.youtube_video_likeBlock_like{
    display: flex;
    gap: 10px;
}
.youtube_video_likeBlock_liked{
    display: flex;
    gap: 10px;
}
.youtube_video_likeBlock_No.like{
    font-weight: 500;
}
.youtubeVideoDivider{
    width: 0px;
    height: 20px;
    border: 1px solid;
}
.youtube_video_about{
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: #a5a5a538;
    width: 100%;
    border-radius: 10px;
    padding: 12px;
    font-weight: 500;
    font-size: 14px;
    gap: 10px;
    margin-top: 10px;
    box-sizing: border-box;
}
.youtubeCommentSection{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.youtubeCommentSectionTitle{
    font-size: 20px;
    font-weight: 500;
}
.youtubeSelfComment{
    display: flex;
    margin-top: 10px;
    gap: 10px;
}
.video_youtubeSelfCommentProfile{
    width: 35px;
    height: 35px;
    border-radius: 50%;
}
.addComment{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.addCommentInput{
    width: 100%;
    background-color: black;
    color: white;
    height: 35px;
    border: none;
    font-size: 16px;
    border-bottom: 1px solid gray;
    &:focus{
        outline: none;
    }
    &::placeholder{
        font-size: 16px;
    }
}
.cancelSubmitCommetn{
    display: flex;
    justify-content: flex-end;
    gap: 15px;
    margin-top: 10px;
}
.cancelComment{
    padding: 8px 16px;
    border-radius: 18px;
    border: 1px solid;
    cursor: pointer;
    &:hover{
        background-color: white;
        color: black;
    }
}
.youtube_video_event_block{
    display: flex;
}
.youtubeOtherComment1{
    display: none;
}



.videoSuggestions{
    width: 100%;
    max-width: 406px;
    padding: 10px 15px;
    gap: 15px;
    display: flex;
    flex-direction: column;
    color: white;
}
.videoSuggestionsBlock{
    display: flex;
    gap: 15px;
    cursor: pointer;
}
.video_suggestion_thumbnail{
    width: 168px;
    height: 94px;
}
.video_suggestion_thumbnail_img{
    width: inherit;
    height: 94px;
}
.video_suggestion_block{
    display: flex;
    flex-direction: column;
    gap: 3px;
}
.video_suggestion_about_title{
    font-size: 15px;
    font-size: 500;
    margin-bottom: 5px;
}
.video_suggestion_about_profile{
    color: #ffffff9c;
    font-size: 12px;
}
.showcomments{
    display: none;
}


@media (max-width: 576px){
    .video{
        margin-top: 56px;
        display: flex;
        flex-direction: column;
        padding: 30px 0;
        justify-content: center;
        background-color: black;
        color: white;
    }
    .video_youtube_video{
        width: 100%;
        border-radius: 10px;
        height: 280px;
    }
    .youtube_video_ProfileBlock_left{
        display: flex;
        gap: 15px;
    }
    .youtubePostProfileSubs{
        display: none;
    }
    .subscribeBtnYoutube1{
        background-color: white;
        color: black;
        padding: 0px 16px;
        border-radius: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 36px;
        font-weight: 600;
        cursor: pointer;
        font-size: 10px;
    }
    .subscribedBtnYoutube1{
        background-color: rgb(244, 107, 107);
        cursor: not-allowed;
        pointer-events: none;
        color: white;
        padding: 0px 16px;
        border-radius: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 36px;
        font-weight: 600;
        cursor: pointer;
        font-size: 10px;
    }
    .youtube_video_likeBlock{
        display: flex;
        gap: 10px;
        background-color: #a5a5a538;
        justify-content: center;
        align-items: center;
        padding: 10px 10px;
        box-sizing: border-box;
        border-radius: 18px;
        cursor: pointer;
    }
    .youtube_video_likeBlock_like{
        display: flex;
        gap: 5px;
    }
    .youtube_video_likeBlock_liked{
        display: flex;
        gap: 5px;
    }
    .youtube_video_likeBlock_No.like{
        font-weight: 500;
    }
    .showcomments{
        display: block;
    }
    .youtubeOtherComment{
        display: none;
    }
}