.fullHomePage1{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    flex: 1;
    min-height: 100vh;
}
.homePage1{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    flex: 1;
    margin-left: 262px;
    min-height: 100vh;
}
.homePage_options1{
    display: flex;
    position: fixed;
    top: 56px;
    z-index: 1;
    width: 100%;
    box-sizing: border-box;
    gap: 10px;
    flex-shrink: 0;
    height: auto;
    overflow-x: scroll;
    background-color: black;
}
.homePage_options1::-webkit-scrollbar{
    width: 12px;
}
.homePage_option1{
    flex: 0 0 auto;
    height: 30px;
    padding: 1px 10px;
    background-color: rgb(42, 42, 42);
    color: white;
    font-weight: 600;
    border-radius: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}


.home_mainPage1{
    display: grid;
    box-sizing: border-box;
    gap: 10px;
    grid-template-columns: 384px 384px 384px;
    padding: 90px 0 20px 13px;
    background-color: black;
    height: 100vh;
}
.fullHome_mainPage1{
    display: grid;
    box-sizing: border-box;
    gap: 30px;
    grid-template-columns: 326px 326px 326px 326px;
    padding: 90px 0 20px 10px;
    background-color: black;
}
.youtube_Video{
    color: white;
    text-decoration: none;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    cursor: pointer;
    height: 316px;
}
.youtube_thumbnailBox{
    width: 100%;
    position: relative;
    box-sizing: border-box;
    height: 216px;
}
.youtube_thumbnailPic1{
    width: 100%;
    height: 100%;
    border-radius: 10px;
}
.youtube_timingThumbnail{
    position: absolute;
    bottom: 0;
    right: 0;
    width: auto;
    padding: 1px 2px;
    background-color: rgb(42, 42, 42);
    border-radius: 5px;
}
.youtubeTitleBox{
    display: flex;
    padding-top: 10px;
}
.youtubeTitleBoxProfile{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.youtube_thumbnail_profile{
    width: 80%;
    border-radius: 50%;
}
.youtubeTitleBox_title{
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}
.youtube_videoTitle{
    font-weight: 600;
    font-size: 16px;
}
.youtube_channelName{
    margin-top: 5px;
    font-size: 18px;
    color: rgb(170, 170, 170);
}
.youtube_videoViews1{
    font-size: 14px;
    color: rgb(170, 170, 170);
}


@media (max-width: 576px) {
    .fullHomePage1{
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
        flex: 1;
        min-height: 100vh;
    }
    .homePage1{
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
        flex: 1;
        margin-left: 262px;
        min-height: 100vh;
    }
    .homePage_options{
        display: flex;
        position: fixed;
        top: 56px;
        z-index: 1;
        width: 100%;
        box-sizing: border-box;
        gap: 10px;
        flex-shrink: 0;
        height: auto;
        overflow-x: scroll;
        background-color: black;
    }
    .home_mainPage1, .fullHome_mainPage1 {
        margin-top: 75px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 12px;
        background-color: black;
        height: 100vh; 
        overflow-y: auto;
        overflow-x: hidden;
        padding: 10px;
        width: 100%;
    }
    .youtube_Video {
        display: flex;
        flex-direction: column;
        width: 100%;
        min-height: 316px;
        box-sizing: border-box;
        cursor: pointer;
        overflow-y: auto;
    } 
    .youtube_videoViews1{
        display: none;
    }
}