.login{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    top: 0;
    color: white;
    display: flex;
    justify-content: center;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-optical-sizing: auto;
}
.login_card{
    width: 40%;
    height: 60%;
    margin-top: 100px;
    background-color: black;
    box-sizing: border-box;
    padding: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0.5px 0.5px 8px white;
}
.titleCard_login{
    font-size: 32px;
    font-weight: 500;
    display: flex;
    gap: 10px;
}
.loginCredentials{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 25px;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.usernameLogin{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.userNameLoginUserName{
    width: 60%;
    height: 45px;
    color: white;
    padding: 0 10px;
    border: none;
    border-radius: 5px;
    background-color: #222222;
}
.login_buttons{
    width: 60%;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}
.login-btn{
    width: 25%;
    border: 1px solid;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    color: white;
    text-decoration: none;
    &:hover{
        color: black;
        background-color: white;
    }
}

@media (max-width: 576px){
    .login_card{
        width: 90%;
        height: 60%;
        margin-top: 100px;
        margin-right: 10%;
        background-color: black;
        box-sizing: border-box;
        padding: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 0.5px 0.5px 8px white;
    }
    .loginCredentials{
        display: flex;
        flex-direction: column;
        gap: 30px;
        margin-top: 25px;
        width: 350px;
        align-items: center;
        justify-content: center;
    }
    .login_buttons{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
    }
}